import React, { useState } from 'react';
import { Button, Container, Typography } from '@mui/material';
import Box from '@mui/material/Box';


//must change the id to unique numbers
const survey = {
    "S1": {
        "question": "Would you be interested in taking part in the survey?",
        "responses": [
            {
                "response": "Yes",
                "id": "yes",
                "next": "S2"
            },
            {
                "response": "No",
                "id": "no",
                "next": "Close"
            }
        ]
    },
    "S2": {
        "question": "Based on the following options, how would you describe your role and responsibility in your household regarding decision making?",
        "responses": [
            {
                "response": "You are the main decision maker",
                "id": "main-decision-maker",
                "next": "S3"
            },
            {
                "response": "You have joint responsibility in the decision making process",
                "id": "joint-responsibility",
                "next": "S3"
            },
            {
                "response": "You are not involved in any way in the final decision-making process",
                "id": "not-involved",
                "next": "not-involved"
            },

        ]
    },
    "not-involved": {
        "question": "Please enter the email of the person responsible for decision making",
        "responses": [
            {
                "response": "We will have an input form to accept the email",
                "id": "input",
                "next": "Close",
                "type": "input",
            }
        ]
    },
    "S3": {
        "question": "Which of the following best apply to you",
        "responses": [
            {
                "response": "We have done some renovations in the past 6 months",
                "id": Math.random() * 17777,
                "next": "S4A"
            },
            {
                "response": "We have built a new home in the past 6 months",
                "id": Math.random() * 17777,
                "next": "S4A"
            },
            {
                "response": "We are planning on building a new home in the next 6 months",
                "id": Math.random() * 17777,
                "next": "S4A"
            },
            {
                "response": "We have done some renovations in the past 6 monthsWe are planning on building a new home in the next 6 months",
                "id": Math.random() * 17777,
                "next": "S4A"
            },
            {
                "response": "We have not made any renovations in the past 6 months and we are not planning of making any ",
                "id": Math.random() * 17777,
                "next": "Close"
            }
            ,
            {
                "response": "We have not built a new home in the past 6 months and are not planning on building any",
                "id": Math.random() * 17777,
                "next": "Close"
            },

        ]
    }, "S4A": {
        "question": " Did you install any cabinets during your renovation or building process?",
        "responses": [
            {
                "response": "Yes",
                "id": Math.random() * 17777,
                "next": "S5"
            },
            {
                "response": "No",
                "id": Math.random() * 17777,
                "next": "S4B"
            }
        ]
    },
    "S4B": {
        "question": " Do you intend to install any cabinets during your renovation or building process?  ",
        "responses": [
            {
                "response": "Yes",
                "id": Math.random() * 17777,
                "next": "S5"
            },
            {
                "response": "No",
                "id": Math.random() * 17777,
                "next": "Close"
            }
        ]
    },
    "S5": {
        "question": " Rest of the survey coming soon...",
        "responses": [
            {
                "response": "Yes",
                "id": Math.random() * 17777,
                "next": "Close"
            },
            {
                "response": "No",
                "id": Math.random() * 17777,
                "next": "Close"
            }
        ]
    },
};

function FormBuilder() {
    const [currentQuestion, setCurrentQuestion] = useState("S1");
    const [responses, setResponses] = useState({});

    const handleResponse = (id) => {
        setResponses({ ...responses, [currentQuestion]: id });
        const nextQuestion = survey[currentQuestion].responses.find(
            (response) => response.id === id
        ).next;
        setCurrentQuestion(nextQuestion);
        console.log(responses)
    };

    return (
        <Container maxWidth="sm">
            <Box sx={{ mt: 2 }}>
                <Typography variant="h4" align="center" gutterBottom>
                    Survey Form
                </Typography>
                {currentQuestion === "Close" ? (
                    <Typography variant="body1" align="center">
                        Thank you for participating in the survey.
                    </Typography>
                ) : (
                    <Box>
                        <Typography variant="h6" gutterBottom>
                            {survey[currentQuestion].question}
                        </Typography>
                        {survey[currentQuestion].responses.map((response) => (
                            <Box key={response.id} sx={{ my: 1 }}>
                                <Button variant="contained" onClick={() => handleResponse(response.id)}>
                                    {response.response}
                                </Button>
                            </Box>
                        ))}
                    </Box>
                )}
            </Box>
        </Container>
    );
}

export default FormBuilder;
